text {
  font-size: 16px;
  font-family: Open Sans, sans-serif;
  fill: #ffffff;
}
.title {
  font-size: 16px;
  fill: #ffffff;
}
text.subTitle {
  font-size: 12px;
  fill: #777777;
}
text.caption {
  font-weight: 400;
  font-size: 14px;
  fill: #777777;
}
text.label {
  /* font-weight: 600; */
  font-family: "Quicksand";
}

text.valueLabel {
  font-weight: 300;
}

text.yearText {
  font-size: 24px;
  font-family: "Quicksand";
  font-weight: lighter;
  /* font-weight: 700; */
  opacity: 1;
}
.tick text {
  fill: #777777;
}
.xAxis .tick:nth-child(2) text {
  text-anchor: start;
}
.tick line {
  shape-rendering: CrispEdges;
  stroke: #dddddd;
}
.tick line.origin {
  stroke: #aaaaaa;
}
path.domain {
  display: none;
}

#race svg{
  /* background: #fff; */
  width: 100%;
}
#race {
  width: 100%;
}