.container {
    height: 100vh;
    background-color: #000;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.container img {
    box-shadow: 0px 0px 150px 0px rgba(25,25,25,0.75);
    width: 400px;
}

.container ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-evenly;
    width: 400px;
    margin-top: 64px;
}

.container ul li::before {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    background-color: #fff;
    margin-bottom: 24px;
}
.container ul li {
    color: #fff;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.container ul .joy::before {
    /* background-color: #FC5302; */
    background-image: url('../../assets/joy.png');
    background-size: contain;

}

.container ul .anger::before {
    /* background-color: #C92A29; */
    background-image: url('../../assets/anger.png');
    background-size: contain;

}

.container ul .sadness::before {
    /* background-color: #2F06FC; */
    background-image: url('../../assets/sad.png');
    background-size: contain;

}

.container ul .fear::before {
    /* background-color: #FFDB16; */
    background-image: url('../../assets/fear.png');
    background-size: contain;

}


.tag {
    display: flex;
    width: 100%;
    position: relative;
    margin-top: 64px;
    margin-bottom: 64px;
}
.tag .left {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.tag .right {
    width: 400px;
    overflow: hidden;
}

.tag .index {
    position: absolute;
    display: block;
    color: #C92A29;
    font-size: 400px;
    top: 50%;
    right: 0;
    transform: translateY(-50%);

}

.instructionSection {
    position: relative;
}

.instruction {
    width: 800px;
    margin: 0 auto;
    color: #fff;
    
}



.instructionSection .index {
    width: 400px;
    position: absolute;
    display: block;
    color: #FC5302;
    font-size: 400px;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.chart {
    box-sizing: border-box;
    /* flex-direction: column; */
    align-items: center;
    width: 800px;
    margin: 64px auto;
    /* padding: 0 64px; */
}

.chart .right{
    width: 40%;

}

.chart .left{
    width: 40%;
    
}
.tagConatiner {
    width: 800px;
    margin: 0 auto;
}

.tagConatiner {
    color: #fff;
    position: relative;
}

.tagDetail li{
    list-style: none;
    font-size: 14px;
    font-weight: lighter;
    transition: all .2s ease-in-out;

